import "./App.css";
import HeaderContainer from "./components/header/HeaderContainer";
import Nav from "./components/nav/Nav";
import { Route, Routes } from "react-router-dom";
import Music from "./components/music/Music";
import Settings from "./components/settings/Settings";
import UsersPage from "./components/users/UsersContainer.tsx";
import React from "react";
import { useNavigate, useParams, useLocation } from "react-router-dom";
import { connect } from "react-redux";
import { initializeApp } from "./redux/appReducer.ts";
import Preloader from "./components/common/preloader/Preloader";
import { withSuspense } from "./hoc/withSuspense.tsx";

const Messages = React.lazy(() => import("./components/messages/Messages"));
const ProfileContainer = React.lazy(() =>
  import("./components/profile/ProfileContainer")
);
const Login = React.lazy(() => import("./components/login/Login"));
const ChatPage = React.lazy(() => import("./pages/ChatPage.tsx"));

const SuspendedMessages = withSuspense(Messages);
const SuspendedProfileContainer = withSuspense(ProfileContainer);
const SuspendedLogin = withSuspense(Login);
const SuspendedChatPage = withSuspense(ChatPage);

class App extends React.Component {
  componentDidMount() {
    this.props.initializeApp();
  }

  render() {
    return (
      <div className="app-wrapper">
        <HeaderContainer />
        <Nav />
        <div className="app-wrapper-content">
          <Routes>
            <Route
              path="/messages"
              element={
                <SuspendedMessages
                  persons={this.props.state.messagePage.persons}
                  messages={this.props.state.messagePage.messages}
                />
              }
            />
            <Route
              path="/profile/:userId"
              element={
                <SuspendedProfileContainer
                  posts={this.props.state.profilePage.posts}
                />
              }
            />
            <Route path="/music" element={<Music />} />
            <Route path="/settings" element={<Settings />} />
            <Route
              path="/users"
              element={<UsersPage store={this.props.store} />}
            />
            <Route path="/authorization" element={<SuspendedLogin />} />
            <Route path="/chat" element={<SuspendedChatPage />} />
          </Routes>
        </div>
      </div>
    );
  }
}

function withRouter(Component) {
  function ComponentWithRouterProp(props) {
    let location = useLocation();
    let navigate = useNavigate();
    let params = useParams();
    return <Component {...props} router={{ location, navigate, params }} />;
  }

  return ComponentWithRouterProp;
}

let mapStateToProps = (state) => ({
  initialized: state.app.initialized,
});

export default connect(mapStateToProps, { initializeApp })(withRouter(App));
