import { UserType, SetUsersDataType } from "../types/types";
//@ts-ignore
import { UserAPI } from "./api.ts";

const FOLLOW: string = "FOLLOW";
const UNFOLLOW: string = "UNFOLLOW";
const SET_USERS: string = "SET_USERS";
const SET_CURRENT_PAGE: string = "SET_CURRENT_PAGE";
const LOADING_PAGE: string = "LOADING_PAGE";
const SET_CURRENT_BUNCH_NUMBER: string = "SET_CURRENT_BUNCH_NUMBER";
const SET_FILTER: string = "SET_FILTER";

let initialState = {
  users: [] as Array<UserType>,
  pageSize: 5,
  totalUsersCount: null as number | null,
  currentPage: 1,
  isFetching: false as boolean | null,
  currentBunchNumber: 1,
  filter: {
    term: "",
    friend: null as null | boolean,
  },
};

export type initialStateType = typeof initialState;
type actionFollowType = {
  type: typeof FOLLOW;
  userId: number;
};
type actionUnfollowType = {
  type: typeof UNFOLLOW;
  userId: number;
};
type actionSetUsersType = {
  type: typeof SET_USERS;
  data: SetUsersDataType;
};
type actionSetCurrentPageType = {
  type: typeof SET_CURRENT_PAGE;
  page: number;
};
type actionSetCurrentBrunchType = {
  type: typeof SET_CURRENT_BUNCH_NUMBER;
  currentBunchNumber: number;
};
type actionLoadingPageType = {
  type: typeof LOADING_PAGE;
  isFetching: boolean;
};
type actionSetFilterType = {
  type: typeof SET_FILTER;
  payload: any;
};

const usersReducer = (
  state: initialStateType = initialState,
  action:
    | actionFollowType
    | actionUnfollowType
    | actionSetUsersType
    | actionSetCurrentPageType
    | actionSetCurrentBrunchType
    | actionLoadingPageType
    | actionSetFilterType
) => {
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        users: state.users.map((u) => {
          //@ts-ignore
          if (u.id === action.userId) {
            return { ...u, followed: true };
          }
          return u;
        }),
      };
    case UNFOLLOW:
      return {
        ...state,
        users: state.users.map((u) => {
          //@ts-ignore
          if (u.id === action.userId) {
            return { ...u, followed: false };
          }
          return u;
        }),
      };
    case SET_USERS:
      return {
        ...state,
        //@ts-ignore
        users: action.data.items,
        //@ts-ignore
        totalUsersCount: action.data.totalCount,
      };

    case SET_CURRENT_PAGE:
      //@ts-ignore
      return { ...state, currentPage: action.page };

    case LOADING_PAGE:
      //@ts-ignore
      return { ...state, isFetching: action.isFetching };

    case SET_CURRENT_BUNCH_NUMBER:
      //@ts-ignore
      return { ...state, currentBunchNumber: action.currentBunchNumber };

    case SET_FILTER:
      //@ts-ignore
      return { ...state, filter: action.filter };

    default:
      return state;
  }
};

export const followAC = (userId: number) => ({ type: FOLLOW, userId });
export const unfollowAC = (userId: number) => ({ type: UNFOLLOW, userId });
export const setUsersAC = (data) => ({ type: SET_USERS, data });
export const setCurrentPageAC = (page) => ({ type: SET_CURRENT_PAGE, page });
export const setFilter = (filter: FilterType) => ({
  type: SET_FILTER,
  filter,
});
export const setCurrentBunchNumberAC = (currentBunchNumber: number) => ({
  type: SET_CURRENT_BUNCH_NUMBER,
  currentBunchNumber,
});
export const loadingPageAC = (isFetching) => ({
  type: LOADING_PAGE,
  isFetching,
});

export const getUsers = (
  page: number,
  pageSize: number,
  filter: FilterType
) => {
  return (dispatch) => {
    dispatch(loadingPageAC(true));
    dispatch(setCurrentPageAC(page));
    dispatch(setFilter(filter));
    UserAPI.getUsersPage(page, pageSize, filter.term, filter.friend).then(
      (data) => {
        dispatch(loadingPageAC(false));
        dispatch(setUsersAC(data));
      }
    );
  };
};

export const followClicked = (userId) => {
  return async (dispatch) => {
    let data = await UserAPI.userFollow(userId);

    if (data.resultCode === 0) {
      dispatch(followAC(userId));
    }
  };
};

export const unfollowClicked = (userId) => {
  return async (dispatch) => {
    let data = await UserAPI.userUnfollow(userId);

    if (data.resultCode === 0) {
      dispatch(unfollowAC(userId));
    }
  };
};

export default usersReducer;

export type FilterType = typeof initialState.filter;
