import { Formik, Form, Field, ErrorMessage } from "formik";
import { FilterType } from "../../redux/usersReducer";
import React from "react";
//@ts-ignore
import s from "./Users.module.css";

const usersSearchFormValidate = (values: any) => {
  const errors = {};
  return errors;
};

type PropsType = {
  onFilterChanged: (filter: FilterType) => void;
};

const UserSearchForm = (props: PropsType) => {
  const submit = (
    values: FilterType,
    { setSubmitting }: { setSubmitting: (isSubmitting: boolean) => void }
  ) => {
    props.onFilterChanged(values);
    setSubmitting(false);
  };

  return (
    <div className={s.color}>
      <Formik
        initialValues={{ term: "", friend: null as null | boolean }}
        validate={usersSearchFormValidate}
        onSubmit={submit}
      >
        {({ isSubmitting }) => (
          <Form className={s.color}>
            <Field className={s.select} name="friend" as="select">
              <option value="null">All users</option>
              <option value="true">Only followed users</option>
              <option value="false">Only unfollowed users</option>
            </Field>
            <Field className={s.textarea} type="text" name="term" />
            <button
              className={s.editButton}
              type="submit"
              disabled={isSubmitting}
            >
              Find
            </button>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default UserSearchForm;
