import { useState } from "react";
//@ts-ignore
import s from "./Paginator.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleLeft } from "@fortawesome/free-solid-svg-icons";
import { faAngleRight } from "@fortawesome/free-solid-svg-icons";
import React from "react";
import { useDispatch } from "react-redux";
//@ts-ignore
import { setCurrentBunchNumberAC } from "../../../redux/usersReducer.ts";

type PropsType = {
  totalUsersCount: number;
  pageSize: number;
  currentBunchNumber: number;
  setCurrentBunchNumber: (currentBunchNumber: number) => void;
  currentPage: number;
  onPageChanged: (currentPage: number) => void;
};

const Paginator = (props: PropsType) => {
  let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);

  let pages: Array<number> = [];
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  const dispatch = useDispatch();

  const setCurrentBunchNumber = (currentBunchNumber) => {
    dispatch(setCurrentBunchNumberAC(currentBunchNumber));
  };

  let bunchSize = 10;
  let bunchesCount = Math.ceil(pagesCount / bunchSize);
  const [numberOfBunch, setNumberOfBunch] = useState(props.currentBunchNumber);
  let numberOfLeftEdge = (numberOfBunch - 1) * bunchSize + 1;
  let numberOfRightEdge = numberOfBunch * bunchSize;

  const clickedRight = () => {
    setNumberOfBunch(numberOfBunch + 1);
    setCurrentBunchNumber(numberOfBunch + 1);
  };

  const clickedLeft = () => {
    setNumberOfBunch(numberOfBunch - 1);
    setCurrentBunchNumber(numberOfBunch - 1);
  };

  return (
    <div style={{ backgroundColor: "#45484e", display: "flex" }}>
      {numberOfBunch > 1 && (
        <span
          style={{ backgroundColor: "#45484e", margin: 0, padding: "8px 5px" }}
          onClick={clickedLeft}
        >
          <FontAwesomeIcon className={s.arrow} icon={faAngleLeft} />
        </span>
      )}
      {pages
        .filter((p) => p >= numberOfLeftEdge && p <= numberOfRightEdge)
        .map((p) => {
          return (
            <span
              className={s.numbers}
              style={
                props.currentPage === p
                  ? {
                      color: "#4676D7",
                      backgroundColor: "#45484e",
                      fontSize: 28,
                      margin: 0,
                      fontWeight: 700,
                    }
                  : { backgroundColor: "#45484e", margin: 0, fontSize: 28 }
              }
              onClick={(e) => {
                props.onPageChanged(p);
              }}
            >
              {p}
            </span>
          );
        })}
      {numberOfBunch !== bunchesCount && (
        <span
          style={{ backgroundColor: "#45484e", margin: 0, padding: "8px 5px" }}
          onClick={clickedRight}
        >
          <FontAwesomeIcon className={s.arrow} icon={faAngleRight} />
        </span>
      )}
    </div>
  );
};

export default Paginator;
