//@ts-ignore
import { UserAPI } from "./api.ts";

const SET_USER_PROFILE: string = "SET_USER_PROFILE";
const SET_STATUS: string = "SET_STATUS";
const SAVE_MAIN_PHOTO: string = "SAVE_MAIN_PHOTO";

let initialState = {
  posts: [
    {
      name: "Mishka Veynov",
      message: "Nobody wants hire me like juke-montage doer?",
      img: "https://fikiwiki.com/uploads/posts/2022-02/1644900950_8-fikiwiki-com-p-kartinki-naik-na-avu-10.jpg",
    },
    {
      name: "Mishka Kamon",
      message: "We are cooler than you in dancing waltz!!!!!",
      img: "https://abali.ru/wp-content/uploads/2014/01/logo_stone_island.png",
    },
  ],
  profile: null as Object | null,
  status: null as string | null,
};

type initialStateType = typeof initialState;
type actionProfileType = {
  type: typeof SET_USER_PROFILE;
  profile: Object;
};
type actionStatusType = {
  type: typeof SET_STATUS;
  status: string;
};
type actionPhotosType = {
  type: typeof SAVE_MAIN_PHOTO;
  photos: Object;
};

const profileReducer = (
  state: initialStateType = initialState,
  action: actionStatusType & actionProfileType & actionPhotosType
) => {
  switch (action.type) {
    case SET_USER_PROFILE:
      return { ...state, profile: action.profile };

    case SET_STATUS:
      return {
        ...state,
        status: action.status,
      };

    case SAVE_MAIN_PHOTO:
      return { ...state, profile: { ...state.profile, photos: action.photos } };

    default:
      return state;
  }
};

const setUserProfileAC = (profile: Object) => ({
  type: SET_USER_PROFILE,
  profile,
});
const setStatusAC = (status: string) => ({ type: SET_STATUS, status });
const saveMainPhotoSuccess = (photos: Object) => ({
  type: SAVE_MAIN_PHOTO,
  photos,
});

export const getProfile = (userId: number) => {
  return async (dispatch: Function) => {
    let data = await UserAPI.profileSet(userId);

    dispatch(setUserProfileAC(data));
  };
};

export const getStatus = (userId: number) => {
  return async (dispatch: Function) => {
    let data = await UserAPI.getStatus(userId);

    dispatch(setStatusAC(data));
  };
};

export const saveMainPhoto = (file: string) => {
  return async (dispatch: Function) => {
    let data = await UserAPI.savePhoto(file);

    if (data.resultCode === 0) {
      dispatch(saveMainPhotoSuccess(data.data.photos));
    }
  };
};

export const saveProfile = (profile: Object) => {
  return async (dispatch: Function, getState: Function) => {
    const userId = getState().auth.id;
    const data = await UserAPI.saveProfile(profile);

    if (data.resultCode === 0) {
      dispatch(getProfile(userId));
    }
  };
};

export const updateStatus = (status: string) => {
  return async (dispatch: Function) => {
    let data = await UserAPI.updateStatus(status);

    if (data.resultCode === 0) {
      dispatch(setStatusAC(status));
    }
  };
};

export default profileReducer;
