import { NavLink, useNavigate } from 'react-router-dom';
import Logotip from './../../images/logotip.png'
import s from './Header.module.css'

const Header = (props) => {
  const navigate = useNavigate()

  const logout = () => {
    props.userLogout()
    navigate('/authorization')
  }
  return (
    <header className={s.header}>
      <a href='/'><img alt='logo' src={Logotip}></img></a>
      {props.isAuth ?
        <div className={s.wrapper}>
          {props.login}
          <button className={s.editButton} onClick={logout}>Logout</button>
        </div> :
        <div className={s.wrapper}>
          <NavLink to='/authorization' style={{ textDecoration: 'none' }}>Login</NavLink>
        </div>}
    </header>
  );
}

export default Header;