import axios from "axios";
import { ProfileType } from "../types/types";

const instance = axios.create({
  withCredentials: true,
  baseURL: "https://social-network.samuraijs.com/api/1.0/",
  headers: {
    "API-KEY": "bd64f03d-3251-4186-aacd-5b0ad39acf76",
  },
});

export const UserAPI = {
  getUsersPage: (
    currentPage: number = 1,
    pageSize: number = 5,
    term: string = "",
    friend: null | boolean = null
  ) => {
    return instance
      .get(
        `users?page=${currentPage}&count=${pageSize}&term=${term}` +
          (friend === null ? "" : `&friend=${friend}`)
      )
      .then((response) => {
        return response.data;
      });
  },
  userFollow: (userId: number) => {
    return instance.post(`follow/${userId}`).then((response) => {
      return response.data;
    });
  },
  userUnfollow: (userId: number) => {
    return instance.delete(`follow/${userId}`).then((response) => {
      return response.data;
    });
  },
  profileSet: (userId: number) => {
    return instance.get(`profile/${userId}`).then((response) => {
      return response.data;
    });
  },
  getStatus: (userId: number) => {
    return instance.get(`profile/status/${userId}`).then((response) => {
      return response.data;
    });
  },
  updateStatus: (status: string) => {
    return instance.put(`profile/status`, { status }).then((response) => {
      return response.data;
    });
  },
  savePhoto: (photoFile: any) => {
    const formData = new FormData();
    formData.append("image", photoFile);

    return axios
      .put(
        `https://social-network.samuraijs.com/api/1.0/profile/photo`,
        formData,
        {
          withCredentials: true,
          headers: {
            "Content-Type": "multipart/form-data",
            "API-KEY": "bd64f03d-3251-4186-aacd-5b0ad39acf76",
          },
        }
      )
      .then((response) => {
        return response.data;
      });
  },
  saveProfile: (profile: ProfileType) => {
    return instance.put(`profile`, profile).then((response) => {
      return response.data;
    });
  },
};

type MeResponseType = {
  data: {
    id: number;
    email: string;
    login: string;
  };
  resultCode: number;
  messages: Array<string>;
};

export const AuthAPI = {
  getAuth: () => {
    return instance.get<MeResponseType>(`auth/me`).then((response) => {
      return response.data;
    });
  },
  login: (
    email: string,
    password: string,
    rememberMe: boolean = false,
    captcha: null | string = null
  ) => {
    return instance
      .post("auth/login", { email, password, rememberMe, captcha })
      .then((response) => {
        return response.data;
      });
  },
  logout: () => {
    return instance.delete("auth/login").then((response) => {
      return response.data;
    });
  },
  getCaptcha: () => {
    return instance.get(`security/get-captcha-url`).then((response) => {
      return response.data;
    });
  },
};
