import { stopSubmit } from "redux-form";
//@ts-ignore
import { AuthAPI } from "./api.ts";

const AUTH_SET_DATA: string = "AUTH_SET_DATA";
const RESET_USER_AUTH_DATA: string = "RESET_USER_AUTH_DATA";
const GET_CAPTCHA_URL: string = "GET_CAPTCHA_URL";

let initialState = {
  id: null as number | null,
  email: null as string | null,
  login: null as string | null,
  isAuth: false as boolean | null,
  captchaUrl: null as string | null,
};

type initialStateType = typeof initialState;
type actionAuthSetData = {
  type: typeof AUTH_SET_DATA;
  data: Object;
};
type actionResetDataType = {
  type: typeof RESET_USER_AUTH_DATA;
};
type actionGetCaptchaType = {
  type: typeof GET_CAPTCHA_URL;
  url: string;
};

const authReducer = (
  state: initialStateType = initialState,
  action: actionAuthSetData & actionGetCaptchaType & actionResetDataType
) => {
  switch (action.type) {
    case AUTH_SET_DATA:
      return {
        ...state,
        ...action.data,
        isAuth: true,
      };
    case RESET_USER_AUTH_DATA:
      return {
        ...state,
        ...initialState,
      };

    case GET_CAPTCHA_URL:
      return {
        ...state,
        captchaUrl: action.url,
      };

    default:
      return state;
  }
};

const authSetDataAC = ({ id, email, login }) => ({
  type: AUTH_SET_DATA,
  data: { id, email, login },
});

const resetAuthDataAC = () => ({ type: RESET_USER_AUTH_DATA });
const getCaptchaSucces = (url: string) => ({ type: GET_CAPTCHA_URL, url });

export const getLogin = () => {
  return async (dispatch: Function) => {
    let data = await AuthAPI.getAuth();

    if (data.resultCode === 0) {
      let { id, email, login } = data.data;
      dispatch(authSetDataAC({ id, email, login }));
    }
  };
};

export const getCaptchaUrl = () => {
  return async (dispatch: Function) => {
    const data = await AuthAPI.getCaptcha();
    const captcha = data.url;
    dispatch(getCaptchaSucces(captcha));
  };
};

export const userLogin = (
  email: string,
  password: string,
  rememberMe: boolean,
  captcha
) => {
  return async (dispatch: Function) => {
    let data = await AuthAPI.login(email, password, rememberMe, captcha);

    if (data.resultCode === 0) {
      dispatch(getLogin());
    } else if (data.resultCode === 10) {
      const message =
        data.messages.length > 0 ? data.messages[0] : "Some error";
      dispatch(getCaptchaUrl());
      dispatch(stopSubmit("login", { _error: message }));
    } else {
      const message =
        data.messages.length > 0 ? data.messages[0] : "Some error";
      dispatch(stopSubmit("login", { _error: message }));
    }
  };
};

export const userLogout = () => {
  return async (dispatch: Function) => {
    let data = await AuthAPI.logout();

    if (data.resultCode === 0) {
      dispatch(resetAuthDataAC());
    }
  };
};

export default authReducer;
