//@ts-ignore
import { getLogin } from "./authReducer.ts";

const SET_INITIALIZED: string = "SET_INITIALIZED";

let initialState = {
  initialized: false as boolean | null,
};

type initialStateType = typeof initialState;
type actionInitializedType = {
  type: typeof SET_INITIALIZED;
  initialized: boolean;
};

const appReducer = (
  state: initialStateType = initialState,
  action: actionInitializedType
) => {
  switch (action.type) {
    case SET_INITIALIZED:
      return {
        ...state,
        initialized: true,
      };

    default:
      return state;
  }
};

const initializedSucces = () => ({ type: SET_INITIALIZED });

export const initializeApp = () => {
  return (dispatch: Function) => {
    let promiseGetLogin = dispatch(getLogin());
    Promise.all([promiseGetLogin]).then(() => {
      dispatch(initializedSucces());
    });
  };
};

export default appReducer;
