import s from './Nav.module.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser } from '@fortawesome/free-regular-svg-icons';
import { faMessage } from '@fortawesome/free-regular-svg-icons';
import { faMusic } from '@fortawesome/free-solid-svg-icons';
import { faGear } from '@fortawesome/free-solid-svg-icons';
import { faUsers } from '@fortawesome/free-solid-svg-icons';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';

const Nav = (props) => {
    return (
        <nav className={s.nav}>
        <div> {props.isAuth
        ? <NavLink to={`/profile/${props.userId}`} className={s.reference}> <FontAwesomeIcon icon={faUser} className={s.icons}/> Profile</NavLink>
        : <NavLink to='/authorization' className={s.reference}> <FontAwesomeIcon icon={faUser} className={s.icons}/> Profile</NavLink>
        }  
        </div>
        <div>
          <NavLink to='/messages' className={s.reference}> <FontAwesomeIcon icon={faMessage} className={s.icons} /> Messages</NavLink>
        </div>
        <div>
          <NavLink to='/users' className={s.reference}> <FontAwesomeIcon icon={faUsers} className={s.icons} /> Users</NavLink>
        </div>
        <div>
          <NavLink to='/music' className={s.reference}> <FontAwesomeIcon icon={faMusic} className={s.icons} /> Music</NavLink>
        </div>
        <div>
          <NavLink to='/settings' className={s.reference}> <FontAwesomeIcon icon={faGear} className={s.icons} /> Settings</NavLink>
        </div>
      </nav>
    );
}

let mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth,
  userId: state.auth.id
})

export default connect(mapStateToProps, {})(Nav);