import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
//@ts-ignore
import s from "./Users.module.css";
//@ts-ignore
import nullUserPhoto from "./../../images/avatar.jpeg";
import { NavLink } from "react-router-dom";
//@ts-ignore
import Paginator from "./paginator/Paginator.tsx";
import { UserType } from "../../types/types";
import {
  FilterType,
  //@ts-ignore
} from "../../redux/usersReducer.ts";
//@ts-ignore
import UserSearchForm from "./UserSearchForm.tsx";
import {
  getCurrentBunchNumber,
  getCurrentPage,
  getPageSize,
  getTotalUsersCount,
  receieveUsers,
  getUsersFilter,
  //@ts-ignore
} from "../../selectors/userSelectors.ts";
import { useDispatch, useSelector } from "react-redux";
import {
  getUsers,
  followClicked,
  unfollowClicked,
  //@ts-ignore
} from "../../redux/usersReducer.ts";

const Users = () => {
  const users: Array<UserType> = useSelector(receieveUsers);
  const pageSize: number = useSelector(getPageSize);
  const totalUsersCount: number = useSelector(getTotalUsersCount);
  const currentPage: number = useSelector(getCurrentPage);
  const currentBunchNumber: number = useSelector(getCurrentBunchNumber);
  const filter: FilterType = useSelector(getUsersFilter);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch<any>(getUsers(currentPage, pageSize, filter));
  }, []);

  const onPageChanged = (page: number) => {
    dispatch<any>(getUsers(page, pageSize, filter));
  };

  const onFilterChanged = (filter: FilterType) => {
    dispatch<any>(getUsers(1, pageSize, filter));
  };

  const follow = (userId: number) => {
    dispatch<any>(followClicked(userId));
  };

  const unfollow = (userId: number) => {
    dispatch<any>(unfollowClicked(userId));
  };

  return (
    <div className={s.wrap}>
      <UserSearchForm onFilterChanged={onFilterChanged} />
      <div className={s.wrapper}>
        <Paginator
          currentBunchNumber={currentBunchNumber}
          totalUsersCount={totalUsersCount}
          pageSize={pageSize}
          currentPage={currentPage}
          onPageChanged={onPageChanged}
        />
        {users.map((u) => (
          <div className={s.wrapping} key={u.id}>
            <div className={s.color}>
              <NavLink
                style={{ backgroundColor: "#45484e" }}
                to={"/profile/" + u.id}
              >
                <img
                  alt="ava"
                  className={s.userPhoto}
                  style={{ width: 100, borderRadius: 50 }}
                  src={u.photos.small === null ? nullUserPhoto : u.photos.small}
                />
              </NavLink>
            </div>
            <div className={s.color}>
              <div className={s.name}>{u.name}</div>
              {u.followed ? (
                <button
                  className={s.followButton}
                  onClick={() => {
                    unfollow(u.id);
                  }}
                >
                  Unfollow
                </button>
              ) : (
                <button
                  className={s.followButton}
                  onClick={() => {
                    follow(u.id);
                  }}
                >
                  Follow
                </button>
              )}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Users;
